.loaderWrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
}

.loader {
  width: 48px;
  height: 48px;
  border: 5px solid #f3f3f3; /* Світло-сірий фон */
  border-top: 5px solid #3498db; /* Синій колір */
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
